import proxy from './proxy.js';

export default (callback, config = {}, dconfig = {}) => {
  if (window.require === undefined) {
    console.log('https://res.wx.qq.com')
    let url = 'https://res.wx.qq.com/open/js/jweixin-1.2.0.js'; // 
    var script = document.createElement("script");
    script.src = url;
    document.head.appendChild(script);
    script.onload = () => {
      wx.config(config);
      wx.ready(() => {
        var mobileSDK = {};
        proxy(wx, mobileSDK, dconfig);
        global.BH_MIXIN_SDK = mobileSDK;
        callback({
          type: 'success',
          sdk: mobileSDK
        });
      });
    };
  } else {
    if (window.wx !== undefined) {
      console.log('APP')
      //APP
      console.log(config)
      window.wx.config(config);
      window.wx.ready((ele) => {
        // wx.checkJsApi({
        //   jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage'], // JSJS2,
        //   success: function(res) {
        //     // apitruefalse
        //     // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
        //     console.log('onMenuShareTimeline-onMenuShareAppMessage')
        //     console.log(res)
        //   }
        // });
        //console.log(ele);
        var mobileSDK = {};
        proxy(wx, mobileSDK, dconfig);
        global.BH_MIXIN_SDK = mobileSDK;
        callback({
          type: 'success',
          sdk: mobileSDK
        });
        // //
        // var imgurl = 'http://ampdx2.wisedu.com/rsfw/sys/rsydzp/public/images/zwgg_d.png';
        // var title = '';
        // var url = 'http://ampdx2.wisedu.com/rsfw/sys/rsydzp/share/ee9239ad09034d5a993614d276d051a0.do';
        // var desc = '';
        // wx.onMenuShareTimeline({
        //     title: title, // 
        //     desc: desc, // 
        //     link: url, // 
        //     imgUrl: imgurl, // 
        //     success: function () {
        //         alert('')
        //         // 
        //     },
        //     cancel: function () {
        //         alert('')
        //         // 
        //     }
        // });
        // wx.onMenuShareAppMessage({
        //   title: title, // 
        //   desc: desc, // 
        //   link: url, // JS
        //   imgUrl: imgurl, // 
        //   type: '', // ,musicvideolinklink
        //   dataUrl: '', // typemusicvideo
        //   success: function () {
        //     alert('')
        //   }
        // });
      });
      window.wx.error(function(res){
        console.log('configerror');
        console.log(res)
        // configerrorconfigdebugresSPA
      });
    } else {
      console.log('require')
      //require
      window.require(['WEIXIN'], function(wx){
        console.log(wx);
        config.beta = true;
        console.log(config)
        wx.config(config);
        wx.ready(() => {
          var mobileSDK = {};
          proxy(wx, mobileSDK, dconfig);
          global.BH_MIXIN_SDK = mobileSDK;
          callback({
            type: 'success',
            sdk: mobileSDK
          });
        });
      });
    }
  }
}



